import { useState, useEffect } from "react";
import { connect } from "react-redux";
import Navbar from "./components/containers/navbar/Navbar";
import AppBody from "./components/containers/appBody/AppBody";

import * as authActionCreators from "./store/actions/Auth";

const App = (props) => {
  const [showSidedrawer, setShowSidedrawer] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState("all");
  const handleShowSidedrawer = () => {
    setShowSidedrawer((prevState) => !prevState);
  };
  const handleSelectedCategory = (category) => {
    setSelectedCategory(category);
  };
  useEffect(() => {
    if (localStorage.hasOwnProperty("authUser")) {
      props.onPersistentLogIn();
    }
  }, [props]);
  return (
    <>
    
      <Navbar
        showTheSidedrawer={showSidedrawer}
        setShowTheSidedrawer={handleShowSidedrawer}
      />
      <AppBody
        showTheSidedrawer={showSidedrawer}
        setShowTheSidedrawer={handleShowSidedrawer}
        selected={handleSelectedCategory}
        selectedCategory={selectedCategory}
      />
    </>
  );
};

const mapDispatchProps = (dispatch) => {
  return {
    onPersistentLogIn: () => dispatch(authActionCreators.persistentLog_in()),
  };
};
export default connect(null, mapDispatchProps)(App);
