import axios from "axios";
import * as ActionTypes from "./ActionTypes";

import firebase from "../../config/firebase-config";
const githubProvider = new firebase.auth.GithubAuthProvider();

export const log_in = () => {
  return (dispatch) => {
    firebase
      .auth()
      .signInWithPopup(githubProvider)
      .then(() => {
        firebase.auth().onAuthStateChanged((authUser) => {
          authUser
            ? localStorage.setItem("authUser", JSON.stringify(authUser))
            : localStorage.removeItem("authUser");

          axios
            .get(`https://api.github.com/user/${authUser.providerData[0].uid}`)
            .then((res) => {
              firebase.firestore().collection("users").doc(res.data.login).set({
                userId: authUser.uid,
                username: res.data.name,
                userImage: res.data.avatar_url,
                githubLink: res.data.html_url,
              });
            });
        });
        dispatch(logger());
      })
      .catch((err) => {
        console.log(err);
      });
  };
};
export const persistentLog_in = () => {
  return (dispatch) => {
    let data = JSON.parse(localStorage.getItem("authUser"));

    axios
      .get(`https://api.github.com/user/${data.providerData[0].uid}`)

      .then((res) => {
        let userLink = res.data.html_url.replace("https://github.com/", "");
        let theUser = {
          username: data.providerData[0].displayName,
          userImage: data.photoURL,
          githubLink: res.data.html_url,
          userLink: userLink,
        };

        dispatch(persist_logger(theUser));
      });
  };
};
export const log_out = () => {
  return (dispatch) => {
    firebase
      .auth()
      .signOut()
      .then(() => {
        dispatch(unLogger());
      });
  };
};

const logger = () => {
  return {
    type: ActionTypes.LOG_IN,
  };
};
const persist_logger = (userInfo) => {
  return {
    type: ActionTypes.PERSISTENT_LOG_IN,
    userInfo: userInfo,
  };
};
const unLogger = (userInfo) => {
  return {
    type: ActionTypes.LOG_OUT,
    userInfo: userInfo,
  };
};
