import * as ActionTypes from "../actions/ActionTypes";

const initialState = {
  isLogged: false,
  user: {
    username: "",
    userImage: "",
    githubLink: "",
  },
};
const AuthReducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionTypes.LOG_IN:
      return {
        ...state,
        isLogged: true,
      };
    case ActionTypes.LOG_OUT:
      localStorage.removeItem("authUser");
      return {
        ...state,
        isLogged: false,
        user: {
          username: "",
          userImage: "",
          githubLink: "",
        },
      };
    case ActionTypes.PERSISTENT_LOG_IN:
      return {
        ...state,
        isLogged: true,
        user: action.userInfo,
      };

    default:
      return state;
  }
};
export default AuthReducer;
