import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { createStore, applyMiddleware } from "redux";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";
import thunk from "redux-thunk";

import auhtReducer from "./store/reducers/Auth";

import "./index.css";
import App from "./App";
import FullPost from "./components/containers/FullPost/FullPost";
import ProfileContainer from "./components/containers/ProfileContainer/ProfileContainer";
import Profile from "./components/containers/ProfileContainer/Profile/Profile";
import LogPage from "./components/containers/ProfileContainer/Profile/LogPage/LogPage";
import AddSnippet from "./components/containers/addSnippetPage/AddSnippetPage";
import EditSnippetPage from "./components/containers/EditSnippetPage/EditSnippetPage";
import NotFound from "./components/containers/NotFound/NotFound";

const store = createStore(auhtReducer, applyMiddleware(thunk));
ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Switch>
        <Route path="/" exact component={App} />
        <Route path="/c/:category" component={App} />
        <Route path="/s/:user/:snippetId" component={FullPost} />
        <Route path="/u/:userId" component={Profile} />
        <Route path="/p" component={ProfileContainer} />
        <Route path="/l" component={LogPage} />
        <Route path="/postSnippet" component={AddSnippet} />
        <Route
          path="/editSnippet/:userId/:snippetId"
          component={EditSnippetPage}
        />
        <Route component={NotFound} />
      </Switch>
    </BrowserRouter>
  </Provider>,
  document.getElementById("root")
);
serviceWorkerRegistration.register();
